  text-align: center;
  line-height: 34px;
  background: #333;
  color: #AAA; }
  .square:focus {
    outline: none; }
  .square.active {
    box-shadow: 0 0 10px 3px inset white, 0 0 10px 3px  white; }
  .square.dead {
    background-color: red; }
    .square.dead::Xbefore {
      content: "X";
      font-weight: 100;
      color: rgba(0, 0, 0, 0.5);
      position: absolute;
      font-size: 43px;
      top: 0;
      left: 2px;
      z-index: 1700; }
  .square .content {
    z-index: 1000;
